body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.markers {
  border-top: 1px solid #ccc;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 15px;
}

.marker {
  -webkit-flex-basis: 25%;
          flex-basis: 25%;
  margin-bottom: 1em;
}

.marker h1 {
  color: #1fffbf;
  font-size: 16px;
}

.marker p {
  margin: 6px 0;
  font-size: 14px;
}

.marker-button {
  background-color: #fff;
  color: #666;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: .3em;
}
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Modal-main {
  position: fixed;
  background: rgba(255,255,255,0.7);
  width: 100%;
  min-height: 100%;
  padding: 1.5em;
}

.Modal-close {
  background: transparent;
  border: 1px solid transparent;
  font-size: 20px;
  float: right;
  padding-right: 15px;
  cursor: pointer;
}

.Modal.isOpen{
  display: block;
}

.Modal.isClosed {
  display: none;
}

.contentmodal {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.contentmodal-title {
  color: #1fffbf;
}

.contentmodal-field {
  margin-bottom: 12px;
}

.contentmodal-label {
  display: block;
  font-size: 14px;
}

.contentmodal-input {
  border-radius: .3em;
  border: 1px solid #ccc;
  padding: 5px;
}

.contentmodal-button {
  background-color: #1fffbf;
  border: 1px solid transparent;
  border-color: #1fffbf;
  border-radius: .3em;
  color: #fff;
  margin: 15px 0 0 15px;
  padding: 5px;
}

.contentmodal-button.disabled {
  background-color: #ccc;
  border: 1px solid #999;
  color: #999;
  pointer-events: none;
}
.full-markers {
  position: absolute;
  width: 400px;
  background-color: white;
  right: 0;
  margin: 30px 100px 0 0;
  height: 50%;
}

